





































































































































































































































































 /*circle loop*/
@keyframes load {
0% {
    stroke-dashoffset: 0;
}
}
.progress {
    position: relative;
    display: inline-block;
    padding: 0;
    text-align: center;
    height: auto;
    min-height: 12rem;
    width: 100%;
    background-color: transparent;
    margin-top: 10px;
    margin-bottom: 10px;
}
.progress > li {
    display: inline-block;
    position: relative;
    text-align: center;
    width:33.33%;
    height: 100%;
    vertical-align: top;
}
.progress .pg-main{
    width: 10rem;
    height: 10rem;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
}
.progress svg {
    width: 100%;
    height: 100%;
    fill:none;
}
div.central-number{
    font-size:18px;
    position: absolute;
    line-height: 25px;
    text-align: center;
}
span.em_2_text{
    width: 2em;
    display: inline-block;
}
/*online users*/
div#online-num{
    top:28px;
    left:20px;
    width:2em;
    color: blue;
}
div#divider{
    top:37px;
    left:37px;
    font-size: 25px;
}
div#total-num{
    top:50px;
    left:42px;
    width:2em;
    color: blue;
}
div.pg-info span.hint-text{
    color:gray;
    font-size: 13px;
    margin-top: 0;
}
div#RAM-percent{
    position: absolute;
    line-height: 25px;
    top:35px;
    left: 20px;
    text-align: center;
    font-size: 25px;
    width:2em;
    color: red;
}
div#RAM-percent-mark{
    position: absolute;
    line-height: 25px;
    top:42px;
    left:60px;
    font-size:13px;
    text-align: center;
}
div.pg-main #stop-rev{
    position: relative;
    top: -105px;
}



