
@media(min-width: 1201px){
div.inst-content{
            position: relative;
            padding-left:215px;
            width:100%;
            z-index: 10;
            padding-top: 2rem;
            max-width: 1350px;
}
div.ctrl-bar{
            width : 205px;
            position: absolute;
            float: left;
            margin-top: 2rem;
            z-index: 12;
            padding-right: 10px;
}
div.ctrl-title{
            font-size: 2.4rem;
            margin-bottom: 1.5rem;
            color:navy;
}
}
@media(max-width: 1201px){
div.inst-content{
            padding-left: 1.5rem;
            padding-right: 1.5rem;
            padding-top: 2rem;
}
div.ctrl-title{
            display: none;
}
}

    /*instance info (general and game-specific) table style*/
table.inst-info-table tr td{
    padding-right: 1.5rem;
    vertical-align: middle;
    box-sizing: border-box;
}
table.inst-info-table tr td:nth-child(1),
table.inst-info-table tr td:nth-child(3)
{
    text-align: right;
    font-weight: bold;
    width: 15%;
}
table.inst-info-table tr td:nth-child(2),
table.inst-info-table tr td:nth-child(4)
{
    width: 35%;
}
span#show-instance-logo{
    display: inline-block;
    width:70px;
    height: 70px;
    padding:2px;
    border:1px solid #9f9f9f;
    border-radius: 2px;
    line-height: 68px;
}
span#show-instance-logo img{
    width: 64px;
    height: 64px;
    display: block;
    position: absolute;
}
div#bottom-image{
    text-align: center;
    width: 64px;
    height: 64px;
    color: #aaaaaa;
    background-color: #f3f3f3;
    font-size: 12px;
    line-height: 64px;
    position: absolute;
}
span#motd{
    display: inline-block;
    max-width: 20rem;
}



