
span.show-instance-logo{
    display: block;
    width:70px;
    height: 70px;
    padding:2px;
    border:1px solid #9f9f9f;
    border-radius: 2px;
    line-height: 68px;
    position: relative;
    float:left;
}
span.show-instance-logo img{
    width: 64px;
    height: 64px;
    display: block;
    position: absolute;
}
div.bottom-image{
    text-align: center;
    width: 64px;
    height: 64px;
    color: #aaaaaa;
    background-color: #f3f3f3;
    font-size: 12px;
    line-height: 64px;
    position: absolute;
}
div.upload-mask{
    position: absolute;
    width: 64px;
    height: 64px;
    background-color: rgba(255,255,255,0.6);
}
span.logo-ctrl{
    display: block;
    position:relative;
    width: 100%;
    padding-left: 90px;
}
span.hint-text{
    font-size: 12px;
    margin-top: 1em;
    display: inline-block;
    color: gray;
}

/*spinning button*/
div.preview{
    text-align: center;
    width: 64px;
    height: 64px;
    position: absolute;
}
div.preview img{
    position: relative;
    display: block;
    width: 64px;
    height: 64px;
}
div.loading{
    text-align: center;
    width: 64px;
    height: 64px;
    position: absolute;
    background-color: #fcfcfc;
    line-height: 64px;
}
div.loading i{
    line-height: 64px;
}
button.upload-like-btn{
    padding: 6px 12px;
    border:none;
    background-color: #d89704;
    line-height: 1.428573;
    font-size: 14px;
    color: white;
    vertical-align: middle;
}
div.main{
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
}



